// Plugin for datamaps to draw points. More about plugins creation - https://web.archive.org/web/20150625160051/http://datamaps.markmarkoh.com/creating-a-datamaps-plugin/
export function pointPlugin(layer, options) {
  const self = this;
  layer.attr('fill', '#000000').attr('opacity', 0.25);
  options = options || {};
  const points = options.points || POINTS_DATA;
  const radius = this.options.element.offsetWidth / 240;
  points.forEach((point, index) => {
    let x, y;
    [x, y] = self.latLngToXY(point[0], point[1]);
    layer.append('circle')
      .attr('cx', x)
      .attr('cy', y)
      .attr('r', radius);
  });
};
